import ServiciosClient from "./ServiciosClient";
import { serviciosData } from "./serviciosData";
import { notFound } from "next/navigation";

// 1. Le decimos a Next.js qué páginas generar
export async function generateStaticParams() {
  return Object.keys(serviciosData).map((slug) => ({
    slug: slug,
  }));
}

// 2. Componente de servidor que resuelve la promesa de los params
export default async function Page({ params }: { params: Promise<{ slug: string }> }) {
  const resolvedParams = await params;
  const { slug } = resolvedParams;
  const data = serviciosData[slug];

  if (!data) {
    notFound();
  }

  return <ServiciosClient data={data} />;
}